
// MIXT BASE STYLESHEET

html,
body {
	height: 100%;
	max-width: 100%;
	margin: 0 !important;
}
body {
	overflow-x: hidden;
	word-wrap: break-word;
	font-family: 'Clear Sans', 'Verdana', sans-serif !important;
}

html.no-scroll,
html.no-scroll body {
	overflow: hidden;
}

// Resets

a,
a:hover,
a:focus { text-decoration: none; }

ul {
	padding-left: 0;
	list-style: none;
}

// Alignment
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}
.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}
.aligncenter {
	clear: both;
	display: block;
	margin: 0 auto;
}


// Images And Wp-captions Should Always Fit And Be Responsive
img {
	display: inline-block;
	height: auto;
	max-width: 100%;
}
img[class*="wp-image-"] {
	margin-top: 10px;
	margin-bottom: 10px;
}
.wp-caption {
	border: 1px solid #ccc;
	margin-bottom: 10px;
	max-width: 100%;
	border: 1px solid #ccc;
	border-radius: 4px;
	padding: 0 10px;
}
.wp-caption.aligncenter {
	margin-bottom: 10px;
}
.wp-caption img[class*="wp-image-"] {
	display: block;
}
.wp-caption .wp-caption-text {
	text-align: center;
	margin-top: 10px;
}

// WP Gallery
.gallery {
	margin-bottom: 1.5em;
}
.gallery a img {
	border: none;
	height: auto;
	max-width: 90%;
}
.gallery dd {
	margin: 0;
}
.gallery-caption {}


// Make Sure Embeds And Iframes Fit Their Containers
embed,
iframe,
object {
	max-width: 100%;
}


// Text Meant Only For Screen Readers
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
}
.screen-reader-text:hover,
.screen-reader-text:active,
.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000; /* Above WP toolbar */
}


/* FONTS */

@font-face {
    font-family: 'Clear Sans';
    src: url('../inc/fonts/ClearSans-Regular-webfont.eot');
    src: url('../inc/fonts/ClearSans-Regular-webfont.eot?#iefix') format('embedded-opentype'),
         url('../inc/fonts/ClearSans-Regular-webfont.woff') format('woff'),
         url('../inc/fonts/ClearSans-Regular-webfont.ttf') format('truetype'),
         url('../inc/fonts/ClearSans-Regular-webfont.svg#clear_sansregular') format('svg');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Clear Sans';
    src: url('../inc/fonts/ClearSans-Medium-webfont.eot');
    src: url('../inc/fonts/ClearSans-Medium-webfont.eot?#iefix') format('embedded-opentype'),
         url('../inc/fonts/ClearSans-Medium-webfont.woff') format('woff'),
         url('../inc/fonts/ClearSans-Medium-webfont.ttf') format('truetype'),
         url('../inc/fonts/ClearSans-Medium-webfont.svg#clear_sansmedium') format('svg');
    font-weight: 500;
    font-style: normal;
}


/* Content */

.main-content-inner {
	padding-bottom: 0;
}
.hentry {
	margin: 0 0 1.5em;
}
.sticky {}
.bypostauthor {}


/* Clearing */

.clear {
	clear: both;
}

/* Navigation */

.dropdown-submenu{position:relative;}
.dropdown-submenu>.dropdown-menu{top:0;left:100%;-webkit-border-radius:0 6px 6px 6px;-moz-border-radius:0 6px 6px 6px;border-radius:0 6px 6px 6px;}
.dropdown-submenu:active>.dropdown-menu, .dropdown-submenu:hover>.dropdown-menu {
display: block;
}
.dropdown-submenu>a:after{display:block;content:" ";float:right;width:0;height:0;border-color:transparent;border-style:solid;border-width:5px 0 5px 5px;border-left-color:#cccccc;margin-top:5px;margin-right:-10px;}
.dropdown-submenu:active>a:after{border-left-color:#ffffff;}
.dropdown-submenu.pull-left{float:none;}.dropdown-submenu.pull-left>.dropdown-menu{left:-100%;margin-left:10px;-webkit-border-radius:6px 0 6px 6px;-moz-border-radius:6px 0 6px 6px;border-radius:6px 0 6px 6px;}

.widget { margin-bottom: 25px; }
.widget_nav_menu ul.sub-menu li, .widget_pages ul.children li { padding-left: 15px; }

.widget select {
	max-width: 100%;
}

.comment .children { margin-left: 15px; }